<template>
  <div class="summary">
    <el-table ref="table" :data="tableData" :border="true">
      <el-table-column
        prop="offerName"
        :label="$t('simMag.offerName')"
        show-overflow-tooltip
        resizable
      >
      </el-table-column>
      <el-table-column
        prop="offerNbr"
        :label="$t('simMag.offerNbr')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="statusCd"
        :label="$t('simMag.statusCd')"
        :formatter="statusFormatter"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="effDate"
        :label="$t('simMag.effDate')"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="expDate"
        :label="$t('simMag.expDate')"
        show-overflow-tooltip
      ></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    tableData: {
      type: Array,
      default: () => {},
    },
  },
  data() {
    return {}
  },
  methods: {
    statusFormatter(row, column) {
      if (column.property === 'statusCd') {
        return row.statusCd === '1000'
          ? this.$t('simMag.statusCdGood')
          : this.$t('simMag.statusCdBad')
      }
    },
  },
}
</script>
<style lang="scss" scoped>
.summary {
  .button-group {
    display: flex;
    ::v-deep .el-button {
      margin: 24px 0px 24px auto;
    }
  }
}
</style>
