<template>
  <div class="container">
    <div class="container-content">
      <div class="summary">
        <div v-for="item in infoList" :key="item.prop" class="summary-item">
          <img :src="item.imgSrc" class="summary-item-icon" />
          <div class="summary-item-content">
            <div class="summary-item-title">{{ item.label }}</div>
            <div class="summary-item-used">
              {{ itemInfoFormatter(item) }}
            </div>
          </div>
        </div>
      </div>
      <div class="container-bottom">
        <div class="seperator-line"></div>
        <el-tabs v-model="activeTab" class="page-tabs page-cont">
          <el-tab-pane :label="$t('traffic.package')" name="1">
            <Package :table-data="offerTableData"></Package>
          </el-tab-pane>
          <el-tab-pane :label="$t('traffic.product')" name="2">
            <Product :table-data="prodTableData"></Product
          ></el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>
<script>
import fetch from '@/request/fetch'
import Product from './product'
import Package from './package'
export default {
  components: {
    Product,
    Package,
  },
  data() {
    return {
      loading: false,
      prodTableData: [],
      offerTableData: [],
      detailInfo: {},
      activeTab: '1',
    }
  },
  computed: {
    infoList() {
      return [
        {
          label: this.$t('traffic.rpNum'),
          formatter: () => this.detailInfo.appProdInstDto.accNum,
          imgSrc: require('@/assets/image/sim/total_use.png'),
        },
      ]
    },
  },
  created() {
    try {
      this.detailInfo = JSON.parse(this.$route.query.row)
    } catch (e) {}
    this.qryMainOfferInstByProdInst()
    this.prodTableData = [this.detailInfo]
  },
  methods: {
    async qryMainOfferInstByProdInst() {
      const param = { prodInstId: this.detailInfo.appProdInstDto.prodInstId }
      const res = await fetch('/offerInst/qryMainOfferInstByProdInst', param, {
        method: 'get',
      })

      this.offerTableData = res ? [res] : []
    },
    itemInfoFormatter(obj) {
      const des = obj.formatter() ?? ''
      return des
    },
  },
}
</script>
